<template>
  <div id="app" :to="callName">
    <router-view></router-view>
    <div v-show="videoState !== 0" class="chat__video" id="im_video">
      <div class="chat__mask" v-show="videoState == 1">
        <div class="callAndOverBox" :expet="epxert">
          <div class="callAndOver" :user="users">
            <div>
              <p style="color: #fff;text-align: center"> {{contact}}正在向您发起协诊视频对话,请尽快处理</p>
            </div>
            <div style="margin-top: 20px;">
              <button type="button" class="btn btn-success  details-btn " @click="callOrOver(1)" style="float: left">接听 </button>
              <button type="button" class="btn btn-warning   details-btn" @click="callOrOver(2)" style="float: right">挂断</button>
            </div>
          </div>
        </div>
      </div>
      <div class="chat__video--container">
        <div v-show="(videoState !== 3)" class="chat__video--title">
          {{contact}}
          <div class="chat__video--duration">
            <!-- 08:23 -->
          </div>
        </div>
        <video v-show="videoState === 2" :class="{'chat__video--full-ing': !chgVedioType, 'chat__video--corner-ing': chgVedioType}" id="video" autoplay=""></video>
        <div v-show="videoState === 2" :class="{'chat__video--full-ing': chgVedioType, 'chat__video--corner-ing': !chgVedioType}">
          <video class="localVideo" id="localVideo" autoplay="" @click="chgVedio"></video>
        </div>
        <div class="chat__video--img shutdown" @click="callOrOver(2)">
          <img src="../static/img/close@2x.png" alt="">
        </div>
        <div class="chat__video--img max" @click="max">
          <img src="../static/img/max@2x.png" alt="">
        </div>
        <div class="chat__video--img min" @click="min">
          <img src="../static/img/min@2x.png" alt="">
        </div>
      </div>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      size="tiny">
      <span>{{expertName}}专家已经发起协诊对话，请及时处理</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" v-on:click="goOder(oderChartData)">去看看</el-button>
  </span>
    </el-dialog>
  </div>
</template>
<style scoped>
  /*.chat__video{width: 400px;height: 400px;position:fixed;z-index: 1000000009;right: 20px;top:70px;}*/


  /*video.block{display: block}*/

  .callAndOverBox {
    width: 250px;
    height: 60px;
    position: absolute;
    left: 50%;
    top: 50%
  }

  .callAndOver {
    width: 250px;
    height: 60px;
    position: absolute;
    left: -50%;
    top: -50%;
    z-index: 11000000000
  }

  .chat__video {
    z-index: 113;
    position: fixed;
    top: 20%;
    left: 20%;
    width: 400px;
    height: 300px;
    overflow: hidden;
    border-radius: 4px;
    background-color: #000;
  }

  .chat__video--container {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .chat__video--title {
    z-index: 1000;
    position: absolute;
    top: 0px;
    height: 40px;
    padding-top: 10px;
    width: 100%;
    text-align: center;
    color: #fff;
  }

  .chat__video--duration {
    font-size: 12px;
    padding: 5px;
  }

  .chat__video--full-ing {
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .chat__video--corner-ing {
    position: absolute;
    background-color: silver;
    cursor: pointer;
    z-index: 400;
    right: 0px;
    bottom: 0px;
    height: 40%;
    width: 23%;
    object-fit: contain;
    overflow: hidden;
  }

  .localVideo {
    height: 100%;
    transform: translateX(-21%);
  }

  .chat__video--img {
    position: absolute;
    cursor: pointer;
    z-index: 5000;
    width: 15px;
    top: 10px;
    height: 15px;
  }

  .chat__video--img img {
    width: 100%;
    height: 100%;
  }

  .max {
    right: 35px;
  }

  .min {
    right: 60px;
  }

  .shutdown {
    right: 10px;
  }
</style>
<script>
  //import { mapGetters } from 'vuex';
  import im from './untils/im.js';
  //  import * as Types from 'untils/store/mutation-types.js';
  //  import bus from '../src/common/common.js';
  //  import WebIM from './untils/easemob.im.js';
  ///*  const WebIMs = require('easemob-websdk');*/
  import publicFn from './untils/Public.js';
  import { mapActions, mapGetters, mapState } from 'vuex'
  export default {
    name: 'app',
    created() {
      // 监听
      // document.onmousedown = this.listeningOperation;
      // document.onscroll = this.listeningOperation;
      // document.onkeypress = this.listeningOperation;
      // 监听
      this.$store.commit('postHttp', this.$http);
      if (localStorage.getItem('access_token')) {
        this.$store.commit('addToken', localStorage.getItem('access_token'));
      }
    },
    data: function() {
      return {
        //是否调用挂断视频方法
        vdiovStatus: true,
        //重连次数
        I: 0,
        //呼叫人
        toCall: null,
        //挂断方式
        endType: true,
        //环信
        conn: WebIM.conn,
        rtcCall: {}, // 视频对话对象
        videoState: 0,   // 0关闭状态 1为等待接受对话 2为对话中 3.等待对方接受
        //挂断方式
        chgVedioType: false,

        massege: [],
        message: null,
        webIMName: null,
        //
        time: { startTime: null, endTime: null },

        vedioType: true,
        //来视频专家名称
        contact: '专家',
        orderId: "",
        oldPosition: {   // 存放上一次的定位位置，用以获取增量
          x: '',
          y: '',
        },
        epxert: null,
        userChart: null,
        dialogVisible:false,
        expertName:"",
        oderChartData:""
      };
    },
    mounted: function() {
      //环信状态监听
      var that = this;
      conn.listen({
        onOpened: function(message) {          //连接成功回调，连接成功后才可以发送消息
          //如果isAutoLogin设置为false，那么必须手动设置上线，否则无法收消息
          // 手动上线指的是调用conn.setPresence(); 在本例中，conn初始化时已将isAutoLogin设置为true
          // 所以无需调用conn.setPresence();
          console.log("%c [opened] 连接已成功建立", "color: green")
        },
        onTextMessage: function(message) {
          // 在此接收和处理消息，根据message.type区分消息来源，私聊;
          message.left = false;//左对其
          message.status = "text";//文本消息
          message.toAndfrm = message.ext.orderId;//聊天订单对象
          if (message.ext.status) {
            that.ApationeList(that.userChart);
            that.postOderChart(message.ext.orderId);
            return
          }
          var time = publicFn.publicLo.dataChange();
          message.time = time.ymdTiem;
          if (message.ext.orderContact) {//视频
            that.massege = message;
            that.contact = message.ext.orderContact;
            that.massege = message;
          }
          //新消息加入vuex显示
          that.$store.commit('pushMasge', message);
          //新消息加入未读状态
          that.$store.commit('pushTookenM', message.toAndfrm);
          //保存记录
          that.savMasge(message);
        },  //收到文本消息
        onEmojiMessage: function(message) {
          // 当为WebIM添加了Emoji属性后，若发送的消息含WebIM.Emoji里特定的字符串，connection就会自动将
          // 这些字符串和其它文字按顺序组合成一个数组，每一个数组元素的结构为{type: 'emoji(或者txt)', data:''}
          // 当type='emoji'时，data表示表情图像的路径，当type='txt'时，data表示文本消息
          var data = message.data;
          for (var i = 0, l = data.length; i < l; i++) {

          }
        },   //收到表情消息
        onPictureMessage: function(message) {//接收图片
          message.left = false;
          message.status = "img";
          message.toAndfrm = message.ext.orderId;
          var time = publicFn.publicLo.dataChange();
          message.time = time.ymdTiem;
          //新消息加入vuex显示
          that.$store.commit('pushMasge', message);
          //新消息加入未读状态
          //that.$store.commit('pushTookenM', message.toAndfrm);
          var options = { url: message.url };
          //保存记录
          that.savMasge(message);
          options.onFileDownloadComplete = function() {
            // 图片下载成功
          };
          options.onFileDownloadError = function() {
            // 图片下载失败
            console.log('Image download failed!');
          };
          WebIM.utils.download.call(conn, options);       // 意义待查

        }, //收到图片消息
        onCmdMessage: function(message) {
          if (message.action) {
            that.ApationeList(that.userChart);
//            that.postOderChart(message.ext.orderId);
            if(message.action==2){
              that.oderChartFn(message.ext.orderId);
//              that.$notify({
//                title: '提示',
//                type:"warning",
//                message: '您有一笔订单已经开始协诊',
//                duration: 0
//              });
            }else {
                //清除上次聊天订单
                this.$store.commit('OderingPostitle',null);
                this.$router.push({ name: 'AssistOrderIng'});
            }
          }
          console.log('CMD');
        },     //收到命令消息
        onAudioMessage: function(message) {
          console.log("Audio");
          that.$store.commit('pushMasge', message);
        },   //收到音频消息
        onLocationMessage: function(message) {
          console.log("Location");
        },//收到位置消息
        onFileMessage: function(message) {
          console.log("File");
          that.$store.commit('pushMasge', message);
        },    //收到文件消息
        onVideoMessage: function(message) {
          var node = document.getElementById('video');
          var option = {
            url: message.url,
            headers: {
              'Accept': 'audio/mp4'
            },
            onFileDownloadComplete: function(response) {
              var objectURL = WebIM.utils.parseDownloadResponse.call(conn, response);
              node.src = objectURL;
            },
            onFileDownloadError: function() {
              console.log('File down load error.')
            }
          };
          WebIM.utils.download.call(conn, option);
        },   //收到视频消息
        onPresence: function(message) {
          switch (message.type) {
            case 'subscribe':                           // 对方请求添加好友
              // 同意对方添加好友
              document.getElementById('agreeFriends').onclick = function(message) {
                conn.subscribed({
                  to: 'asdfghj',
                  message: "[resp:true]"
                });
                // 需要反向添加对方好友
                conn.subscribe({
                  to: message.from,
                  message: "[resp:true]"
                });
              };
              // 拒绝对方添加好友
              document.getElementById('rejectFriends').onclick = function(message) {
                conn.unsubscribed({
                  to: message.from,
                  message: "rejectAddFriend"                  // 拒绝添加好友回复信息
                });
              };
              break;
            case 'subscribed':                          // 对方同意添加好友，已方同意添加好友
              break;
            case 'unsubscribe':                         // 对方删除好友
              break;
            case 'unsubscribed':                        // 被拒绝添加好友，或被对方删除好友成功
              break;
            case 'joinChatRoomSuccess':                 // 成功加入聊天室
              console.log('join chat room success');
              break;
            case 'joinChatRoomFaild':                   // 加入聊天室失败
              console.log('join chat room faild');
              break;
            case 'joinPublicGroupSuccess':              // 意义待查
              console.log('join public group success', message.from);
              break;
            case 'createGroupACK':
              conn.createGroupAsync({
                from: message.from,
                success: function(option) {
                  console.log('Create Group Succeed');
                }
              });
              break;
          }
        },       //收到联系人订阅请求（加好友）、处理群组、聊天室被踢解散等消息
        onRoster: function(message) {

        },         //处理好友申请
        onInviteMessage: function(message) {

        },  //处理群组邀请
        onOnline: function() {
          that.longHx();
        },                  //本机网络连接成功
        onOffline: function() {
          that.longHx();
        },                 //本机网络掉线
        onError: function(message) {
          if (that.I > 9) {
            return
          }
          if (message && message.type == 1) {
            console.warn('连接建立失败！请确认您的登录账号是否和appKey匹配。')
          } else if (message.type === 16 || message.type == 32 || message.type == 7) {//重新连接
            that.longHx();
            that.I += 1;
          } if (message.type === 8) {

          } else {
            that.longHx();
            that.I += 1;
          }
        },           //失败回调
        onBlacklistUpdate: function(list) {
          // 查询黑名单，将好友拉黑，将好友从黑名单移除都会回调这个函数，list则是黑名单现有的所有好友信息
          console.log(list);
        }     // 黑名单变动
      });
      this.linsert();//
      // this.registerUser();

      this.dragable();
    },
    methods: {
      ...mapActions({
        ApationeList: 'ApationeList',
        postOderChart: "postOderChart",
      }),
      //开始协诊订单信息
      oderChartFn(id){
        var url='/api/assist-order/assistOrders/'+id;
        var token= localStorage.getItem('access_token');
        this.$http.get(url,{
          headers:{
            Authorization: 'bearer '+token
          }
        }).then(function(res){
          var res=res.body;
          this.dialogVisible=true;
          this.expertName=res.expertName;
          this.oderChartData=res
        }).catch(function (res) {
          this.dialogVisible=true;
          this.expertName="有一位"
        });
      },
      //全局提示 弹框
      goOder(data){
        this.dialogVisible = false;
        this.$router.push({ name: 'AssistOrderIng', query: { id: data.expertId, orderNo: data.id, paitornt: data.patientId, doctorId: data.doctorId } });
      },
      //环信
      longHx: function() {//环信登录
        var par = localStorage.getItem('easemob');
        var webim_ = localStorage.getItem('webim_');
        if (par.easemobUser) {
          var options = {};
          par = JSON.parse(par);
          if (webim_) {
            options = { apiUrl: WebIM.config.apiURL, user: par.name, pwd: par.password, appKey: WebIM.config.appkey };//帐号密码登录
          } else {
            options = { apiUrl: WebIM.config.apiURL, user: par.name, accessToken: webim_, appKey: WebIM.config.appkey };//to登录
          }
          conn.open(options);
        }

      },
      callOrOver: function(a) {
        if (!this.vdiovStatus) {
          this.videoState = 0;
          return
        }
        if (a == 1) {
          this.videoState = 2;
          this.rtcCall.acceptCall();
        }
        if (a == 2) {
          this.videoState =0;
          this.rtcCall.endCall();
          this.$store.commit('pushCall', null);
        }
      },
      dragable: function() {
        const imVideo = document.getElementById('im_video');
        const app = document.getElementById('app');
        const position = (e) => {
          if (this.oldPosition.x && this.oldPosition.y) { // 排除首次的错误变化率
            const deltX = e.screenX - this.oldPosition.x;
            const deltY = e.screenY - this.oldPosition.y;
            imVideo.style.left = `${imVideo.offsetLeft + deltX}px`;
            imVideo.style.top = `${imVideo.offsetTop + deltY}px`;
          }
          this.oldPosition.x = e.screenX;
          this.oldPosition.y = e.screenY;
        };
        imVideo.addEventListener('mousedown', () => {
          imVideo.addEventListener('mousemove', position, false);
        }, false);
        app.addEventListener('mouseup', () => {
          this.oldPosition.x = '';
          this.oldPosition.y = '';
          imVideo.removeEventListener('mousemove', position, false);
        }, false);
      },
      //拨打
      videoCall: function() {//发起视频聊天
        this.rtcCall.caller = this.userChart.easemobUser.username,
          this.rtcCall.makeVideoCall(this.toCall);
        this.videoState = 3;
      },
      //接受
      acceptCall: function() {//接收视频聊天
        this.videoState = 2;
      },
      //挂断
      endCall: function() {//挂断视频聊天
        that.videoState = 0;
        this.rtcCall.endCall();
        this.$store.commit('pushCall', null);
      },
      //视频最小化
      min: function() {
        const imVideo = document.getElementById('im_video');
        imVideo.style.top = '20%';
        imVideo.style.left = '20%';
        imVideo.style.width = '400px';
        imVideo.style.height = '300px';
      },
      max: function() {
        const imVideo = document.getElementById('im_video');
        imVideo.style.top = 0;
        imVideo.style.left = 0;
        imVideo.style.width = '100%';
        imVideo.style.height = '100%';
      },
      chgVedio: function() {
        // this.chgVedioType = !this.chgVedioType;
      },
      addContactAndMessage: function(msg) {
        //      this.$store.commit(Types.ADD_MSG, msg); // 全局对话存储
        //      this.$store.commit(Types.ADD_CONTACT, msg);
      },
      linsert: function() {
        this.rtcCall = null;
        var that = this;
        if (WebIM.WebRTC) {
          this.rtcCall = new WebIM.WebRTC.Call({
            connection: conn,
            mediaStreamConstaints: {
              audio: true,
              video: true
            },
            listener: {
              onAcceptCall: function(from, options) {
                that.videoState = 2; // 更改videoState状态为通话中
              },
              onGotRemoteStream: function(stream, streamType) {
                console.log("onGotRemoteStream");
                var video = document.getElementById('video');
                video.srcObject = stream;
              },
              onGotLocalStream: function(stream, streamType) {
                console.log("onGotLocalStream");
                var video = document.getElementById('localVideo');
                video.srcObject = stream;
                that.videoState = 2; // 更改videoState状态为通话中 当接受他人视频邀请时使用
              },
              onRinging: function(caller) {
                console.log(caller, "onRinging");
                that.videoState = 1;
              },
              onTermCall: function(reason) {
                console.log('reason:', reason);
                this.videoState = 0;
              },
              onIceConnectionStateChange: function(iceState) {
                console.log(iceState, "iceState");
                if (iceState == "closed") {
                  that.videoState = 0;
                }
                if(iceState =='disconnected'){
                  that.warning("对方视频已经断开连接")
                }
              },
              onError: function(e) {
                console.log(e, "callee");
                that.vdiovStatus = false;
                that.$store.commit('pushCall', null);
                if (e.message == "callee is not online!") {
                  that.open("对方不在线");
                }
                return true
              }
            }
          });
        } else {
          console.warn('不能进行视频通话！您的浏览器不支持webrtc或者协议不是https。')
        }
      },
      //保存聊天消息
      savMasge: function(list) {
        if (this.userChart.userType == 2) {
          return;
        }
        var parm = {};
        var date = publicFn.publicLo.dataChange();
        date = date.ymdTiem;
        if (list.status == "text") {
          parm = {
            "createdBy": 0,
            "dialogueContent": JSON.stringify({ data: list.data, ext: list.ext }),
            "dialogueTime": "2017-08-03T12:59:58.563Z",
            "id": 0,
            "orderId": list.ext.orderId,
            "updatedBy": 0,
            "updatedWhen": "2017-08-03T12:59:58.563Z",
            "type": 1,
            "userId": 0
          }
        }
        if (list.status == "img") {
          parm = {
            "createdBy": 0,
            "dialogueContent": JSON.stringify({ data: list.url, ext: list.ext }),
            "dialogueTime": "2017-08-03T12:59:58.563Z",
            "id": 0,
            "orderId": list.ext.orderId,
            "updatedBy": 0,
            "updatedWhen": "2017-08-03T12:59:58.563Z",
            "type": 2,
            "userId": 0
          }
        }
        parm = JSON.stringify(parm);
        var url = '/api/assist-order/uDialogueRecord';
        var token = localStorage.getItem('access_token');
        this.$http.post(url, parm, {
          headers: {
            Authorization: 'bearer ' + token
          }
        }).then(function(res) {
          res = res.body;

        }).catch(function(e) {

        });
      },
      open: function(text) {
        this.$message(text);
      },
      success: function() {
        this.$message({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        });
      },
      warning: function(text) {
        this.$message({
          message: text,
          type: 'warning'
        });
      },
    },

    computed: {
      expertId: function() {
        this.epxert = this.$store.state.Odering;
        return this.epxert
      },
      callName: function() {
        var dat = this.$store.state.call;
        if (dat) {
          this.toCall = dat;
          this.videoCall();
        }
        return this.toCall
      },
      users: function() {
        var data = this.user;
        if (data) {
          this.userChart = data
        }
        return 1
      },
      ...mapState([
        'oderChart',
        'user'
      ]),
    },
    // 监听
    listeningOperation() {

    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 0px;
    height: 100%;
  }
</style>



