import Vue from 'vue';
import Vuex from 'vuex';
import actionsTooking from './actionsTooking.js';
import stateTooking from './stateTooking.js';
import mutationsTooking from './commiTooking.js';
import $ from 'jquery';
// 卫其模块
import test from './sunweiqi/SWQ-store.js'
// 卫其模块
Vue.use(Vuex);
//store初始值
var state = { key: 0 };
var actions = { key: () => {} };
var mutations = { key: () => {} };
// Store组装
state = $.extend({}, state, stateTooking.stateTooking);
mutations = $.extend({}, mutations, mutationsTooking.commitTooking);
actions = $.extend({}, actions, actionsTooking.tookingAryy);
export const store = new Vuex.Store({
    state: state,
    mutations: mutations,
    actions: actions,
    modules: {
        test
    }
});



