// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource';
import { store } from './untils/vuex/store.js';
import moment from 'moment' //日期插件
import ElementUi from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

//import store from './untils/store/index.js';
Vue.use(VueResource);
Vue.use(ElementUi);
Vue.config.productionTip = false;
Vue.config.silent = true; //去掉vue所有的警告跟日志
Vue.config.devtools = false; //是否让vue检查代码
import '../static/css/base.css'
// 使用基于vue的模块
Vue.use(VueResource);
Vue.config.productionTip = false;
// 定義一個過濾器
Vue.filter('diseaseFilter', function(input) {
    if (input == "asthma") {
        return '哮喘急診'
    } else if (input == "cardiovascular") {
        return '心血管'
    } else if (input == "electrophysiology") {
        return '心電生理'
    }
});
// 日期过滤器
Vue.filter('dataFilter', function(input) {
    if (!input) { return '' }
    return moment(input).format('YYYY-MM-DD')
});

//时间过滤
Vue.filter('dataTimeFilter', function(input) {
    if (!input) { return '' }
    return moment(input).format('YYYY-MM-DD HH:mm:ss')
});

//时分秒过滤
Vue.filter('timeFilter', function(input) {
    if (!input) { return '' }
    return moment(input).format('HH:mm:ss')
});

// 收支明细状态过滤器
Vue.filter('moneyMes', function(input) {
    if (input == 1) {
        return '已完成'
    } else if (input == 2) {
        return '提现中'
    } else if (input == 3) {
        return '已完成'
    } else if (input == 4) {
        return '提现失败'
    } else if (input == 0) {
        return '待确认'
    }
})

new Vue({
    el: '#app',
    router,
    store: store,
    template: '<App/>',
    components: { App }
})


