const state = {
    test: 12,
    test1: 13,
    imgUrl: {
        blowUpImgUrl: '',
        isBlowUp: false
    }
}
const mutations = {
    changeTest(state) {
        state.test++
    },
    changeImgUrl(state, parameter) {
        state.imgUrl = parameter
    }
}
const actions = {
    CHANGTEST({ commit }) {
        commit('changeTest')
    },
    CHANGEiMGURL({ commit }, parameter) {
        commit('changeImgUrl', parameter)
    }

}
const getter = {
    testFilter(state) {
        return state.test % 2 == 0 ? 'even' : 'odd'
    }
}
export default {
    state,
    actions,
    mutations,
    getter
}


