import publicFn from '../Public.js';
var tookingAryy={
  //病例列表
  recodeListFn:({commit, state},parm)=>{
    var url="/api/patient/records/today?userId="+parm;
    state.http.get(url,{
      headers:{
        Authorization: 'bearer '+state.token
      }
    }).then(function(res){
      res=res.body;
     commit('postRecodeList',res)
    });
  },
  //订单详情
  postOderChart:({commit, state},list)=>{
    var id=list;
    var url='/api/assist-order/assistOrders/'+id;
    state.http.get(url,{
      headers:{
        Authorization: 'bearer '+state.token
      }
    }).then(function(res){
      var res=res.body;
      commit('pushOderChart',res)
    }).catch(function (res) {
      commit('pushOderChart',-100)
    });
  },
  //协整患者列表正在协诊
  ApationeList:({dispatch,state},user)=>{
    var result={
      listIng:{listin:null,listWal:null},
      listWall:null
    };
    var parm={
      state:2,
      pageNumber:1,
      pageSize:30,
    };
    if(user.userType==1){
      parm.doctorId=user.id
    }
    if(user.userType==2){
      parm.expertId=user.id
    }
    var url="/api/assist-order/assistOrders/web";
      state.http.get(url,{
      headers:{
        Authorization: 'bearer '+state.token
      },
        params:parm
    }).then(function(res){
        res=res.body.list;
        res=publicFn.publicLo.sortData(res,'startTime',false);
        result.listIng.listin=res;
        dispatch("ApationeLisToday",{result:result,type:user.userType,id:user.id})
    });
  },
  //协整患者列表预今天约中
  ApationeLisToday:({dispatch, state},data)=>{
    var parme={
      state:1,
      pageNumber:1,
      pageSize:30,
    };
    if(data.type==1){
      parme.doctorId=data.id
    }
    if(data.type==2){
      parme.expertId=data.id
    }
      var da=publicFn.publicLo.dataChange();
      parme.startDate = da.today;
      parme.endDate = da.today;
    var url='/api/assist-order/assistOrders/web';
    state.http.get(url,{
      headers:{
        Authorization: 'bearer '+state.token
      },
      params:parme
    }).then(function(res){
      res=res.body.list;
      res=publicFn.publicLo.sortData(res,'startTime',false);
      data.result.listIng.listWal=res;
      dispatch('ApationeLisWall',data);
    });
  },
  //协整患者列表后续预约
  ApationeLisWall:({commit, state},data,type,id)=>{
    var parme={
      state:1,
      pageNumber:1,
      pageSize:30,
    };
    if(data.type==1){
      parme.doctorId=data.id
    }
    if(data.type==2){
      parme.expertId=data.id
    }
    var startDate=publicFn.publicLo.dataChange();
        startDate=startDate.nexDate;
        parme.startDate=startDate
    var url='/api/assist-order/assistOrders/web';
    state.http.get(url,{
      headers:{
        Authorization: 'bearer '+state.token
      },
      params:parme
    }).then(function(res){
      res=res.body.list;
      res=publicFn.publicLo.sortData(res,'startTime',false);
      data.result.listWall=res;
      commit('postPatientList',data.result);
    });
  },
  //liao天记录
  tookinglist:({commit, state},parm)=>{
    var url='/api/assist-order/uDialogueRecord/'+ parm;
    state.http.get(url,{
      headers:{
        Authorization:'bearer '+state.token
      }
    }).then(function(res){
      var tookingList=[];
      res=res.body;
      if(!res[0]){
        return
      }
      for(let i in res){
        var time=publicFn.publicLo.timeCheng(res[i].createdWhen);
        time=time.times;
        if(res[i].dialogueContent.data){
          res[i].dialogueContent.data="";
        }
        var par=res[i].dialogueContent;
        par = JSON.parse(res[i].dialogueContent);
        var status,left;
        if( res[i].type==1){
          status='text';
        }
        if( res[i].type==2){
          status='img';
        }
        if(res[i].userId==0 && state.user.userType==1){
          left=false
        }
        if(res[i].userId==1 && state.user.userType==1){
          left=true
        }
        if(res[i].userId==0 && state.user.userType==2){
          left=true
        }
        if(res[i].userId==1 && state.user.userType==2){
          left = false
        }
        if(!par.ext.appRecordId){
          par.ext.appRecordId=null
        }
        var date={
          "id" : res[i].id,
          "userId" : res[i].userId,
          "time" :time,
          "data" :par.data,
          "toAndfrm" : res[i].orderId,
           ext:par.ext,
           left:left,
          "status" :status,
        };
        if( res[i].type==2){
          date.url=par.data
        }
        tookingList.push(date);
      }
      if(!tookingList[0]){
        return
      }
      commit('pushHostryMasge',tookingList)
    })
  },
};
export default{tookingAryy}



